'use client'

import { usePathname } from 'next/navigation'
import { AnimatePresence, motion } from 'framer-motion'
import { useEffect, useState } from 'react'

export default function TransitionLayout({ children }: { children: React.ReactNode }) {
  const pathname = usePathname()
  const [direction, setDirection] = useState(1)
  const [prevPath, setPrevPath] = useState(pathname)

  // Direction logic
  useEffect(() => {
    if (prevPath === '/post' && pathname === '/find') {
      setDirection(-1)
    } else if (prevPath === '/find' && pathname === '/post') {
      setDirection(1)
    } else {
      setDirection(0)
    }
    setPrevPath(pathname)
  }, [pathname, prevPath])

  // Color based on destination page
  const background =
    pathname === '/post'
      ? 'rgb(152,183,191)' // cadet gray
      : pathname === '/find'
      ? 'rgb(241,180,151)' // apricot
      : 'white'

  return (
    <AnimatePresence mode="wait">
      <motion.div
        key={pathname}
        initial={{ x: `${100 * direction}%`, opacity: 0 }}
        animate={{ x: 0, opacity: 1 }}
        exit={{ x: `${-100 * direction}%`, opacity: 0 }}
        transition={{ duration: 0.5, ease: 'easeInOut' }}
        className="min-h-screen w-screen overflow-hidden"
        style={{ background }}
      >
        {children}
      </motion.div>
    </AnimatePresence>
  )
}
